
//操作事件类型

export enum EventType {

  NONE = 0,

  //主页面刷新新的Spine数据
  REFRESH_SKELETON = 1,

  //切换Spine
  CHANGE_SPINE = 2,

  //设置Animation
  CHANGE_SPINE_ANIMATION = 3,

  //更新皮肤
  UPDATE_SPINE_SKIN = 4,

  //开启Debug模式
  OPENOROFF_DEBUG = 5,

  //设置循环模式
  CHANGE_SPINE_LOOP = 6,
}


export class Eventparam {

  public eventType: EventType = EventType.NONE;

  public param: any = null;

}